
import Vue from 'vue'
import util,{_} from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [common,globals],
components: {
Popup,
Flexbox,
FlexboxItem
},
props: {
value: {
type: [String,Number,Array],
default: []
}
},
data () {
return {
popupShow: false,
model: [],
check_all: false,
}
},
watch:{
popupShow: function (val) {
if(val){
this.model = util.copy(this.value)
}
},
value: function (val) {
this.model = util.copy(val)
},
model: function (val) {
if(this.branchList){
this.check_all = (val.length === this.branchList.length)
}
}
},
mounted () {
this.initBids()
},
methods: {
initBids () {
let result = [],
bids = Vue.localStorage.get('x-bid')

bids = bids?JSON.parse(bids):[]

if(bids&&bids.length>0){
result = bids
}else{
result = [this.bid$[0]]
}

this.$store.commit('updateBid',result)
},
ok () {
if(!this.model.length){
this.toast('请选择校区')
return false
}
this.$emit('input',this.model)
this.$emit('on-change',this.model)
this.popupShow = false
},
getClass (row) {
return {[`active`]: this.model==row.bid||(_.isArray(this.model)&&this.model.indexOf(row.bid)>-1)}
},
reset () {
this.model = []
},
checkAll () {
this.check_all = !this.check_all

this.model = []
if(this.check_all){
this.branchList.forEach(b=>{
this.model.push(b.bid)
})
}

},
toggleCheck (row) {
if(this.multiple){
let index = this.model.indexOf(row.bid)
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push(row.bid)
}
}else{
this.model = row.bid
}
}
},
computed: {
multiple(){
if(Array.isArray(this.model)){
return true
}
return false
},
branchText () {
let result = '请选择校区'
if(this.value.length>0){
result = this.$filter('branch_name')(this.value[0])
}
if(this.value.length>1){
result += '等'+this.value.length+'个校区'
}
return result
},
branchList () {
let list = this.$store.state.gvars.branchs,
result = list
if(list&&list.length){
if(this.user$.employee.rids.indexOf(10)===-1){
let bids = this.user$.employee.bids
result = []

bids.forEach(b=>{
let index = list.findIndex(l=>l.bid===b)
if(index>-1){
result.push(list[index])
}
})
}

}
return result
}
}
}



