import util from '@/libs/util'
import {_} from '@/libs/util'
import common from '@/libs/common.mixin'
import VueVideo from 'c%/vueVideo.vue'

export default{
	mixins: [ common ],
	components: {
		VueVideo
	},
	data () {
		return {
			isPlay: false
		}
	},
	props: {
		item: {
			type: Object,
			default: ()=>{
				return {}
			}
		}
	},
	computed: {
		sources () {
			let item = this.item,
				result = []
			if(!_.isEmpty(item)){
				result = [{
					src: item.file_url,
					size: item.file_size,
					type: 'video/mp4'
				}]
			}
			return result
		}
	},
	methods: {
		play () {
			this.isPlay = !this.isPlay
			this.$refs.video.play()
		},
		getSource (item) {
			return [{
				src: item.file_url,
				size: item.file_size,
				type: 'video/mp4'
			}]
		},
		getVOptions (item) {
			return {
				autoplay: false,
		        volume: 0.6,
		        poster: item.src+'?vframe/jpg/offset/1'				
			}
		},
		getDuration (item) {			
			let duration = Math.ceil(item.duration)
			return util.second_to_minute(duration)				
		},
	}
}



