
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'

import Popup from 'vux/src/components/popup/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Panel from 'vux/src/components/panel/index.vue'


export default{
mixins: [ common, modal ],
components: {
Popup,
Badge,
Panel
},
data () {
return {
lesson_name: '',
rest_api: 'lesson_standard_files',
currentLesson: {},
CourseListPanel: false
}
},
mounted () {
this.init_data()
},
methods: {
handleClick (item) {
this.$emit('on-selected',item.files)
this.CourseListPanel = false
this.close()
},
chooseCourse (item) {
if(this.lessonStandardFileSum(item.lid) > 0){
this.currentLesson = item
this.CourseListPanel = true
}
},
lessonStandardFileSum (lid) {
let list = this.$store.state.lesson_standard_file
return list.filter(l=>l.lid==lid).length
},
init_data () {
let params = {
with: 'lesson_standard_file_item',
page: 1,
pagesize: 10000
},
list = this.$store.state.lesson_standard_file

if(list.length>0){
return
}

this.$rest(this.rest_api).get(params)
.success(data=>{
this.$store.commit('updateLessonStandardFile',data.list)
}).error(response=>{
this.toast(response.body.message||'获取课标失败~','warn')
})
},
getImgSrc (list) {
let type = list[0].file_type,
media_type = list[0].media_type,
result = ''

if(media_type=='voice'){
result = '/src/assets/file-voice.png'
}else if(media_type=='image'){
result = list[0].file_url
}else if(media_type=='video'){
result = list[0].file_url + '?vframe/jpg/offset/1/w/170/h/170)'
}else{
if(type.indexOf('doc')>-1){
result = 'w'
}
else if(type.indexOf('ppt')>-1){
result = 'p'
}
else if(type.indexOf('xls')>-1){
result = 'x'
}else{
result = 'file'
}
result = '/src/assets/'+result+'.png'
}

return result
}
},
computed: {
currentCourseList () {
let list = this.$store.state.lesson_standard_file||[],
result = []

list = list.filter(l=>l.lid==this.currentLesson.lid)

list.forEach(l=>{
let obj = {
files: l.lesson_standard_file_item,
src: '',
title: l.title,
desc: this.labelDicts(l.csft_did,'course_standard_file_type') + '共（'+ l.lesson_standard_file_item.length +'个文件）',
fallbackSrc: 'http://placeholder.qiniudn.com/60x60/3cc51f/ffffff',
url: ''
}
if(l.lesson_standard_file_item.length>0){
obj.src = this.getImgSrc(l.lesson_standard_file_item)
}else{
obj.src = '/src/assets/file.png'
}

result.push(obj)
})

return result
},
filterLessons () {
let list = this.gvars('lessons')
if(!/^\s*$/.test(this.lesson_name)){
list = list.filter(l=>l.lesson_name.indexOf(this.lesson_name)>-1)
}

return list
},
style () {
return {
zIndex: 1003,
height: `calc(100% - ${this.headerHeight}px)`,
top: this.headerHeight + 'px',
overflowX: 'hidden'
}
}
}
}




