
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import AvatarAndName from 'c%/AvatarAndName.vue'
import SelectRole from 'c%/SelectRole.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


const resName   = 'employees'
const idField   = 'eid'
const textField = 'ename'

export default{
mixins: [ common,globals ],
components: {
Cell,
Popup,
Flexbox,
FlexboxItem,
SelectRole,
AvatarAndName
},
props: {
width: {
type: String,
default: '80%'
},
title: {
type: String,
default: '选择员工'
},
value: {
type: [String,Number,Array],
default: ()=>{
return []
}
},
did: {
type: Number,
default: 0
},
},
data () {
return {
popupShow: false,
model: [],
check_all: false,
data: [],
search_value: '',
idField: idField,
textField: textField,
toggleExpand: false,
}
},
watch:{
popupShow: function (val) {
if(val){
this.model = util.copy(this.value)
}
},
value: function (val) {
this.model = util.copy(val)
},
model: function (val) {
this.check_all = (this.model.length === this.data.length)
}
},
methods: {
close () {
this.popupShow = false
this.model = util.copy(this.value)
},
getModelIndex (row) {
let model = this.model,
index = -1
if(model.length){
index = this.did?model.findIndex(m=>m.eid==row.eid):model.indexOf(row.eid)
}
return index
},
loadFinished () {
let data = this.data
if(this.did){
if(data&&data.length){
data.forEach(d=>{
this.$set(d,'sale_role_id',0)
})
}
}
},
del (i) {
let index = this.model.indexOf(i)

if(index>-1){
this.model.splice(index,1)
}
},
toggle () {
this.toggleExpand = !this.toggleExpand
},
getClass (row) {
let index = -1
if(this.multiple&&this.model.length){
index = this.did?this.model.findIndex(m=>m.eid==row[idField]):this.model.indexOf(row[idField])
}

return [
{[`active`]: !this.multiple&&this.model==row[idField]},
{
[`checked`]: index>-1
}
]

},
ok () {
if(this.did){
this.getDeputy().then(()=>{
this.$emit('input',this.model)
this.popupShow = false
})
}else{
this.$emit('input',this.model)
this.popupShow = false
}
},
getDeputy () {
let data = this.data,
model = this.model,
errContent = '',
errList = []

return new Promise((resolve,reject)=>{
if(model.length){
errList = model.filter(m=>m.sale_role_id==0)
if(errList.length){
errList.forEach(e=>{
let obj = data.find(d=>d[idField]==e.eid)

errContent += obj.ename + '、'
})
}

}
if(errList.length){
errContent = errContent.substring(0,errContent.length-1)
this.alert(util.sprintf('请给责任人【%s】选择角色',errContent))

reject()
}else{
resolve()
}
})

},
reset () {
this.model = []
},
checkAll () {
this.check_all = !this.check_all

this.model = []
if(this.check_all){
this.data.forEach(b=>{
this.model.push(b[idField])
})
}

},
toggleCheck (row) {
if(this.did){
let index = this.model.findIndex(m=>m.eid==row.eid)
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push({
eid: row.eid,
sale_role_id: 0
})
}
}else{
if(this.multiple){
let index = this.model.indexOf(row[idField])
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push(row[idField])
}
}else{
this.model = row[idField]
}
}
},
getName (id) {
let data = this.data,
result = ''
if(data.length>0&&id>0){
let obj = data.find(s=>s[idField]===id)
if(obj){
result = obj[textField]
}
}
return result
}
},
computed: {
checkHeight () {
let height = 0
if(this.multiple){
if(!this.did){
height += 50
}
height += 50
}

return height
},
scrollTop () {
/*vux-header + x-popup-header + x-popup-search*/
return this.headerHeight + 50 + 39 + this.checkHeight
},
scrollHeight () {
/*vux-header + x-popup-header + x-popup-search + x-popup-footer*/
return this.headerHeight + 50 + 39 + 40 + this.checkHeight
},
multiple(){
if(Array.isArray(this.value)){
return true
}
return false
},
labelText () {
let model = this.model,
result = '请选择'

if(this.multiple){
if(model.length>0){
if(this.did){
result = this.getName(model[0].eid)+'-'+this.labelDicts(model[0].sale_role_id,'sale_role')
}else{
result = this.getName(model[0])
}

}
if(model.length>1){
result += '等'+model.length+'人'
}

}else{
result = this.getName(model)
}
return result
},
filter_data () {
let val = this.search_value,
data = this.data
if(!/^\s*$/.test(val)){
let filter = (item) => {
return item.pinyin_abbr.indexOf(val)>-1||item.pinyin.indexOf(val)>-1||item[textField].indexOf(val)>-1
}
return data.filter(filter)
}
return data

},
data_uri () {
return resName
}
}
}



