export default{
	props: {
		fileIds: {
			type: Array,
			default: ()=>{
				return []
			}
		},
		mode: {
			type: String,
			default: 'list'
		},
		count: {
			type: Number,
			default: 0
		}
	},
	data () {
		return {
			show: false,
			operate: '',
			map_operate_text: {'del': '删除','push': '推送'}				
		}
	},
	computed: {
		operateText () {
			let operate_text = this.map_operate_text[this.operate] 
			
			if(this.mode=='select'){
				operate_text = '确定'
			}
			
			if(this.count > 0){
				return `${operate_text}(${this.fileIds.length}/${this.count})`	
			}
			
			return `${operate_text}(${this.fileIds.length})`
		}
	},
	methods: {
		operateClick () {
			this.$emit('operate',this.operate)
		},
		toggle () {				
			if(!this.operate){
				this.show = !this.show	
			}else{
				this.operate = ''
				this.$emit('on-click',false)
			}				
		},
		addInfo () {
			this.show = false	
		},
		delInfo () {
			this.operate = 'del'
			this.$emit('on-click',true)
			this.show = false
		},
		pushInfo () {
			this.operate = 'push'
			this.$emit('on-click',true)
			this.show = false
		}
		
	}
}



