import moment from 'moment'
import util from '@/libs/util'
import wxjssdk from '@/libs/wxjssdk.mixin'
import VueVideo from 'c%/vueVideo.vue' 
import SmallVoice from 'c%/SmallVoice.vue'
import PlayVideo from 'c%/PlayVideo.vue'

export default{
	mixins: [ wxjssdk ],
	components: {
		VueVideo,
		SmallVoice,
		PlayVideo,
	},
	data () {
		return {
			imgPriview:false
		}
	},
	props: {
		file: {
			type: Object,
			default: ()=>{
				return {}
			}
		}
	},
	methods: {		
		previewVideo (file) {
			this.$refs.playVideo
			.set('video',file)
			.show()
		},
		getDuration (item) {			
			let duration = Math.ceil(item.duration)
			return util.second_to_minute(duration)				
		},
		getFileType (item) {
			let type = item.file_type,
				result = ''
			if(type.indexOf('doc')>-1){
				result = 'w'
			}
			else if(type.indexOf('ppt')>-1){
				result = 'p'
			}
			else if(type.indexOf('xls')>-1){
				result = 'x'
			}else{
				result = 'file'
			}
			return '/src/assets/'+result+'.png'
		},	
		preview (item) { 
			this.previewImage(item.file_url,[item.file_url])
		}			
	},
	computed: {
		
	}
}



