import Avatar from 'c%/Avatar.vue'
export default {
	components: {
		Avatar
	},
	props: {
		src: {
			type: String,
			default: ''
		},
		name: {
			type: String,
			default: ''
		},
		nickname:{
			type: String,
			default: ''
		},
		shape: {
			type: String,
			default: 'circle'
		},
		size: {
			type: String,
			default: 'default'
		},
		sex: {
			type: [Number,String],
			default: 0
		}
	},
	data() {
		return {
			sex_map: {1: '男',2: '女'}
		}
	},
	methods: {
		click() {
			this.$emit('click')
		}
	}
}



