
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


const resName   = 'classes'
const idField   = 'cid'
const textField = 'class_name'

export default{
mixins: [ common,globals ],
components: {
Cell,
Popup,
Flexbox,
FlexboxItem
},
props: {
width: {
type: String,
default: '80%'
},
title: {
type: String,
default: '选择班级'
},
value: {
type: [String,Number,Array],
default: []
},
placeholder: {
type: String,
default: '请选择'
}
},
data () {
return {
data: [],
popupShow: false,
model: util.copy(this.value),
check_all: false,
search_value: '',
idField: idField,
textField: textField
}
},
watch:{
popupShow: function (val) {
if(val){
this.model = util.copy(this.value)
}
},
value: function (val) {
this.model = util.copy(val)
},
model: function (val) {
this.check_all = (this.model.length === this.data.length)
}
},
methods: {
getClass (row) {
return {[`active`]: this.model==row[idField]}
},
ok () {
let obj = {}

if(!this.multiple){
obj = this.data.find(d=>d[idField]==this.model)
this.$emit('on-selected',obj)
}

this.$emit('input',this.model)
this.popupShow = false
},
reset () {
this.model = this.multiple?[]:0
},
checkAll () {
this.check_all = !this.check_all

this.model = []
if(this.check_all){
this.data.forEach(b=>{
this.model.push(b[idField])
})
}

},
toggleCheck (row) {
if(this.multiple){
let index = this.model.indexOf(row[idField])
if(index>-1){
this.model.splice(index,1)
}else{
this.model.push(row[idField])
}
}else{
this.model = row[idField]
}
},
getName (id) {
let result = ''
if(this.data){
let obj = this.data.find(d=>d[idField]==id)
if(obj){
result = obj[textField]
}
}

return result
}
},
computed: {
scrollTop () {
/*vux-header + x-popup-header + x-popup-search*/
return this.headerHeight + 50 + 39
},
scrollHeight () {
/*vux-header + x-popup-header + x-popup-search + x-popup-footer*/
return this.headerHeight + 50 + 39 + 40
},
multiple(){
if(Array.isArray(this.value)){
return true
}
return false
},
labelText () {
let model = this.model,
result = this.placeholder
if(this.multiple){
if(model.length>0){
result = this.getName[0]
}
if(model.length>1){
result += '等'+model.length+'个班级'
}

}else{
if(model){
result = this.getName(model)
}
}
return result
},
filter_data () {
let val = this.search_value,
data = this.data

if(!/^\s*$/.test(val)){
let filter = (item) => {
return item[textField].indexOf(val)>-1
}
return data.filter(filter)
}
return data

},
data_uri () {
return 'classes?my=true&teach_eid='+this.eid$
}
}
}



