import Locale from '@/libs/locale.mixin';

const prefixCls = 'ivu-page';

function isValueNumber (value) {
    return (/^[1-9][0-9]*$/).test(value + '');
}

export default {
    name: 'PageOption',
    mixins: [ Locale ],
    props: {
        pageSizeOpts: Array,
        showSizer: Boolean,
        showElevator: Boolean,
        current: Number,
        _current: Number,
        pageSize: Number,
        allPages: Number,
        isSmall: Boolean,
        placement: String
    },
    data () {
        return {
            currentPageSize: this.pageSize
        };
    },
    watch: {
        pageSize (val) {
            this.currentPageSize = val;
        }
    },
    computed: {
        size () {
            return this.isSmall ? 'small' : 'default';
        },
        optsClasses () {
            return [
                `${prefixCls}-options`
            ];
        },
        sizerClasses () {
            return [
                `${prefixCls}-options-sizer`
            ];
        },
        ElevatorClasses () {
            return [
                `${prefixCls}-options-elevator`
            ];
        }
    },
    methods: {
        changeSize () {
            this.$emit('on-size', this.currentPageSize);
        },
        changePage (event) {
            let val = event.target.value.trim();
            let page = 0;

            if (isValueNumber(val)) {
                val = Number(val);
                if (val != this.current) {
                    const allPages = this.allPages;

                    if (val > allPages) {
                        page = allPages;
                    } else {
                        page = val;
                    }
                }
            } else {
                page = 1;
            }

            if (page) {
                this.$emit('on-page', page);
                event.target.value = page;
            }
        }
    }
};



