
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import FilterItem from './filter/filter-item.vue'
import SelectEmployee from 'c%/SelectEmployee.vue'
import SelectLesson from 'c%/SelectLesson.vue'
import SelectClass from 'c%/SelectClass.vue'
import Group from 'vux/src/components/group/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


const list = [
{dict: 'grade',model: 'school_grade', title: '年级',default: 0},
{dict: 'customer_status',model: 'customer_status_did',title: '客户状态',default: 0},
{dict: 'from',model: 'from_did',title: '招生来源',default: 0},
{com: 'SelectEmployee',model: 'follow_eid',title: '主责任人',default: 0},
{com: 'SelectEmployee',model: 'second_eid',title: '副责任人',default: 0}
]

export default{
mixins: [ common, modal ],
components: {
Popup,
Group,
Flexbox,
FilterItem,
FlexboxItem,
SelectLesson,
SelectClass,
SelectEmployee
},
props: {
dictList: {
type: Array,
default: ()=>{
return {}
}
},
value: {
type: Object,
default: ()=>{
return {}
}
}
},
data () {
return {
customer_status: [],
filterList: [],
filter: util.copy(this.value)
}
},
watch: {
'modal$.show': function (val){
if(val) {
this.filter = util.copy(this.value)
}
}
},
methods: {
initFilter () {
let dictList = this.dictList
if(dictList&&dictList.length){
dictList.forEach(d=>{
this.filter[d.model] = util.copy(d.default)
})
}
},
getFilterList (type) {
return this.dictList.filter(l=>l[type])
},
ok () {
this.$emit('input',this.filter)
this.$emit('on-change',this.filter)
this.close()
},
reset () {
this.initFilter()
}
}
}



