
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'

import Upload from 'c%/upload'
import FileItem from './selectfile/FileItem.vue'
import CourseStandardFile from './selectfile/CourseStandardFile.vue'
import MineFile from './selectfile/MineFile.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default{
mixins: [ common,modal ],
props: {
value: {
type: Array,
default: ()=>{
return []
}
},
count: {
type: Number,
default: 0
},
fileType: {
type: String,
default: ''
}
},
directives: {
TransferDom
},
components: {
Upload,
Popup,
MineFile,
Flexbox,
FileItem,
FlexboxItem,
CourseStandardFile
},
data () {
return {
bottom: 0,
uploadList: this.value,
contentShow: false
}
},
watch: {
'modal$.show': function (val) {
if(!val){
setTimeout(()=>{
this.contentShow = false
},400)
}else{
this.contentShow = true
}
}
},
computed: {
format () {
if(this.fileType=='image'){
return ['jpg','jpeg','png']
}else{
return []
}
},
max () {
return this.count - this.uploadList.length
},
style () {
return {
zIndex: 1002,
height: '244px'
}
},
upload_header(){
return {
'x-token':this.$store.state.user.token,
'x-file-key':'file'
}
},
upload_post(){
return {
mod:'prepare'
}
},
showAddBtn () {
if(this.count > 0){
return this.uploadList.length < this.count
}else{
return true
}
}
},
methods: {
parsePercentage (val) {
return parseInt(val, 10);
},
handleProgress (event, file, fileList) {
let temp_file = Object.assign({},file,{_percent:0}),
index = this.uploadList.findIndex(u=>u.uid==temp_file.uid)
if(index==-1){
temp_file.file_url = temp_file.url
this.uploadList.push(temp_file)
}else{
this.uploadList[index]._percent = file.percentage
}

this.currentFile = file
if(this.count>0){
if(this.uploadList.length + fileList.length > this.count){
this.alert(
'超出上传限制',
'最多允许上传 ' + this.count + '张图片',
true
);
return false
}
}
},
handleSuccess (res, file) {
let data = res.data,
index = this.uploadList.findIndex(u=>u.uid==file.uid)

if(index == -1){

this.uploadList.push(res.data)
}else{
this.uploadList[index]._percent = 100
setTimeout(()=>{
this.uploadList.push(res.data)
this.uploadList.splice(index,1)
},100)
}
},
handleSelect (list) {

if(this.count > 0 && list.length > this.max) {
this.toast('所选文件数量超过了限制数量，请重新选择','text')
return
}

this.uploadList = this.uploadList.concat(list)

this.$emit('input',this.uploadList)
},
chooseFile (type) {
if(type!=='local'){
this.$refs[type].show()
}

this.modal$.show = false
},

}

}



