import util from '@/libs/util' 
export default{
	props: {
		file: {
			type: Object,
			default: ()=>{
				return {}
			}
		},
		status: {
			type: Boolean,
			default: ()=>{
				return false
			}
		},
		editable: {
			type: Boolean,
			default: ()=>{
				return false
			}
		},
		hasCheck: {
			type: Boolean,
			default: ()=>{
				return false
			}
		},
		checked: {
			type: Boolean,
			default: ()=>{
				return false
			}
		}
	},
	data () {
		return {
			currentTime: 0,
			isPlaying: false,
		}
	},
	watch: {
		status: function (val) {
			if(this.file.media_type=='voice'){
				if(val){
					this.play()
				}else{
					this.ended()
				}				
			}
		},
	},
	computed: {
		progress () {
			let duration = parseInt(this.file.duration || 0),
				currentTime = this.currentTime,
				result = Math.ceil((currentTime / duration) * 100)
			
			return result
		},
		totalTimeText () {
			return util.second_to_minute(Math.ceil(this.file.duration))
		},
		currentTimeText () {
			return util.second_to_minute(Math.ceil(this.currentTime))
		}
	},
	methods: {
		getFileType () {
			let type = this.file.file_type,
				result = ''
			if(type.indexOf('doc')>-1){
				result = 'w'
			}
			else if(type.indexOf('ppt')>-1){
				result = 'p'
			}
			else if(type.indexOf('xls')>-1){
				result = 'x'
			}else{
				result = 'file'
			}
			return '/src/assets/'+result+'.png'
		},
		toggle () {
			if(this.editable&&this.hasCheck){
				this.$emit('toggleCheck',this.file)					
			}
		},
		previewImage () {
			this.$emit('previewImage',this.file)
		},
		previewVideo () {
			this.$emit('previewVideo',this.file)
		},
		togglePlay () {
			if(!this.isPlaying){					
				this.play()
			}else{
				this.stop()
			}
		},
		addCurrentTime () {
			if(this.$refs.player.interval){
				window.clearInterval(this.$refs.player.interval)
			}
			
			this.$refs.player.interval = setInterval(()=>{
				if(this.isPlaying){
					this.currentTime += 1
				}
			},1000)
		},
		play () {			
			this.isPlaying = true
			this.addCurrentTime()
			
            this.$refs.player.play()	            
	    },
	    stop () {
	    	window.clearInterval(this.$refs.player.interval)
	    	this.isPlaying = false
	    	
            this.$refs.player.pause()
	    },
	    ended () {
	    	this.currentTime = 0
	    	this.$refs.player.currentTime = 0
	    	this.isPlaying = false
	    	this.$refs.player.pause()
	    	window.clearInterval(this.$refs.player.interval)
	    },
	}
}



