
import moment from 'moment'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import VueVideo from 'c%/vueVideo.vue'
import SmallVoice from 'c%/SmallVoice.vue'
import FileItemVideo from './FileItemVideo.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Previewer from 'vux/src/components/previewer/index.vue'


export default{
mixins: [ common ],
components: {
Previewer,
VueVideo,
SmallVoice,
FileItemVideo,
},
data () {
return {
imgPriview:false,
active_voice_id: 0
}
},
directives: {
TransferDom
},
props: {
list: {
type: Array,
default: ()=>{
return []
}
},
fullWidth: {
type: Boolean,
default: false
}
},
methods: {
setActiceVoiceId (item) {
this.active_voice_id = item.file_id
},
getFileImg (item) {
let type = item.file_type,
result = ''
if(type.indexOf('doc')>-1){
result = 'w'
}
else if(type.indexOf('ppt')>-1){
result = 'p'
}
else if(type.indexOf('xls')>-1){
result = 'x'
}else{
result = 'file'
}
return '/src/assets/'+result+'.png'
},
previewVideo (item) {
this.$refs.playVideo
.set('video',item)
.show()
},
previewImage (item) {
let list = this.file_list('image')
let index = list.indexOf(item)

this.imgList = []
if(list.length){
list.forEach(l=>{
this.imgList.push({
src: l.file_url
})
})
}
this.previewShow = true

setTimeout(()=>{
this.$refs.previewer.show(index)
},100)
},
file_list (type) {
let [result,uploadList] = [[],this.list]
if(uploadList.length>0){
let filter = (item)=>{
if(typeof item.media_type == 'string'){
return item.media_type == type
}else{
type = this.getFileTypes(type)

return type.indexOf(item.file_type)>-1
}
}

result = uploadList.filter(filter)
}
return result
},
getFileTypes (type) {
if(type=='image'){
type = ['image','gif','jpg','jpeg','png','bmp','webp']
}else if(type=='voice'){
type = ['voice','mp3','wav','wma','ogg','aac','flac']
}else if(type=='video'){
type = ['video','mp4','m3u8','rmvb','avi','swf','3gp','mkv','flv']
}else{
type = ['numbers','csv','xls','xlsx','keynote','ppt','pptx']
}
return type
}
},
computed: {
wrapClass () {
let length = this.file_list('image').length
return {
[`file-container`]: true,
[`file-container2`]:length==2,
[`file-container1`]:length==1||this.fullWidth
}
}
}
}



