
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import MineFile from 'v%/home/media.vue'

import Popup from 'vux/src/components/popup/index.vue'


export default{
mixins: [ common, modal ],
props: {
count: {
type: Number,
default: 0
},
fileType: {
type: String,
default: ''
}
},
components: {
MineFile,
Popup
},
data () {
return {

}
},
methods: {
handleSelect (list){
this.$emit('on-selected',list)
this.close()
}

},
computed: {
style () {
return {
zIndex: 1003,
top: `${this.headerHeight}px`
}
}
}
}



