import moment from 'moment'
import common from '@/libs/common.mixin'

export default{
	mixins: [common],
	data () {
		return {
			bid: [],
			yearMonth: moment(new Date()).format('YYYY-MM'),
		}
	},
	methods: {
		preMonth () {
			this.yearMonth = moment(moment(this.yearMonth).subtract(1,'months')).format('YYYY-MM')
		},
		nextMonth () {
			this.yearMonth = moment(moment(this.yearMonth).add(1,'months')).format('YYYY-MM')
		},
	},
	computed: {
		bids: {
			get: function (val) {
				if(this.bid.length===0){
					let list = this.$store.state.gvars.branchs
					if(list&&list.length){		
						
						if(this.user$.employee.rids.indexOf(10)===-1){						
							this.bid = this.user$.employee.bids 
						}else{
							this.bids = []
							list.forEach(b=>{
								this.bid.push(b.bid)							
							})	
						}
					}
				}
				return this.bid
			},
			set: function (val) {
				this.bid = val
			}
		},
		maxYear () {
			return parseInt(moment(new Date()).format('YYYY'))
		},
		title () {
			return moment(this.yearMonth).format('YYYY年MM月')
		}
	}
}



