import renderHeader from './header';
import Mixin from './mixin';

export default {
    name: 'TableHead',
    mixins: [ Mixin ],
    components: { renderHeader },
    props: {
        prefixCls: String,
        styleObject: Object,
        columns: Array,
        objData: Object,
        data: Array,    // rebuildData
        columnsWidth: Object,
        fixed: {
            type: [Boolean, String],
            default: false
        }
    },
    computed: {
        styles () {
            const style = Object.assign({}, this.styleObject);
            const width = this.$parent.bodyHeight === 0 ? parseInt(this.styleObject.width) : parseInt(this.styleObject.width) + this.$parent.scrollBarWidth;
            style.width = `${width}px`;
            return style;
        },
        isSelectAll () {
            let isSelectAll = true;
            if (!this.data.length) isSelectAll = false;
            if (!this.data.find(item => !item._disabled)) isSelectAll = false;    // #1751
            for (let i = 0; i < this.data.length; i++) {
                if (!this.objData[this.data[i]._index]._isChecked && !this.objData[this.data[i]._index]._isDisabled) {
                    isSelectAll = false;
                    break;
                }
            }

            return isSelectAll;
        }
    },
    methods: {
        cellClasses (column) {
            return [
                `${this.prefixCls}-cell`,
                {
                    [`${this.prefixCls}-hidden`]: !this.fixed && column.fixed && (column.fixed === 'left' || column.fixed === 'right')
                }
            ];
        },
        itemClasses (column, item) {
            return [
                `${this.prefixCls}-filter-select-item`,
                {
                    [`${this.prefixCls}-filter-select-item-selected`]: column._filterChecked[0] === item.value
                }
            ];
        },
        itemAllClasses (column) {
            return [
                `${this.prefixCls}-filter-select-item`,
                {
                    [`${this.prefixCls}-filter-select-item-selected`]: !column._filterChecked.length
                }
            ];
        },
        selectAll () {
            const status = !this.isSelectAll;
            this.$parent.selectAll(status);
        },
        handleSort (index, type) {
            const column = this.columns[index];
            const _index = column._index;

            if (column._sortType === type) {
                type = 'normal';
            }
            this.$parent.handleSort(_index, type);
        },
        handleSortByHead (index) {
            const column = this.columns[index];
            if (column.sortable) {
                const type = column._sortType;
                if (type === 'normal') {
                    this.handleSort(index, 'asc');
                } else if (type === 'asc') {
                    this.handleSort(index, 'desc');
                } else {
                    this.handleSort(index, 'normal');
                }
            }
        },
        handleFilter (index) {
            this.$parent.handleFilter(index);
        },
        handleSelect (index, value) {
            this.$parent.handleFilterSelect(index, value);
        },
        handleReset (index) {
            this.$parent.handleFilterReset(index);
        },
        handleFilterHide (index) {
            this.$parent.handleFilterHide(index);
        }
    }
};



