
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Popup from 'vux/src/components/popup/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'


export default{
mixins: [common,globals],
components: {
Popup,
Flexbox,
FlexboxItem
},
props: {
value: {
type: [String,Number,Array],
default: []
},
title: {
type: String,
default: '选择角色'
},
showFooter: {
type: Boolean,
default: false
}
},
data () {
return {
popupShow: false,
model: [],
check_all: false,
}
},
watch:{
popupShow: function (val) {
if(val){
this.model = util.copy(this.value)
}
},
value: function (val) {
this.model = util.copy(val)
}
},
methods: {
ok () {
this.$emit('input',this.model)
this.popupShow = false
},
reset () {
this.model = 0
},
toggleCheck (row) {
this.model = row.did
if(!this.showFooter){
setTimeout(()=>{
this.ok()
},50)
}
}
},
computed: {
labelText () {
let result = '请选择角色'
if(this.value){
result = this.labelDicts(this.value,'sale_role')
}
return result
}

}
}



