
import util from '@/libs/util'
import modal from '@/libs/modal.mixin'
import common from '@/libs/common.mixin'
import PushConfirm  from './push-confirm.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default{
mixins: [ common, modal ],
components: {
Tab,
Cell,
Popup,
Group,
XButton,
TabItem,
PushConfirm
},
props: {
fileIds: {
type: Array,
default: ()=>{
return []
}
}
},
data () {
return {
tab: 0,
search_value: '',
classList: [],
studentList: {
class: [],
list: []
}
}
},
mounted () {
this.init_data()
},
computed: {
filterClasses () {
let val = this.search_value,
list = this.classList,
filter = (item)=>{
return true
}

if(!/^\s*$/.test(val)){
filter = (item)=>{
return item.class_name.indexOf(val)>-1
}
}

return list.filter(filter)
},
style () {
return {
top: this.headerHeight+'px'
}
},
file_package_file () {
let ids = this.fileIds,
result = []
if(ids&&ids.length>0){
ids.forEach(i=>{
result.push({file_id:i})
})
}
return result
}
},
methods: {
filterStudents (list) {
let val = this.search_value,
filter = (item)=>{
return true
}

if(!/^\s*$/.test(val)){
filter = (item)=>{
return item.student_name.indexOf(val)>-1||item.pinyin.indexOf(val)>-1||item.pinyin_abbr.indexOf(val)>-1
}
}

return list.filter(filter)
},
pushSuccess () {
this.$emit('on-success')
this.close()
},
pushInfo (item) {
let name = item.sid>0?item.student_name:item.class_name,
info = {
bid: this.bid$[0]||0,
cid: item.sid>0?0:item.cid,
sid: item.sid,
file_package_file: this.file_package_file,
is_push: 1,
title: ''
}

this.$refs.confirm
.set('name',name)
.set('info',info)
.show('','add')
},
init_data () {
this.getMyClass()
this.getMyStudents()
},
getMyClass() {
let params = {
students:1
}
let uri = util.sprintf('employees/%s/classes',this.eid$)
this.$rest(uri).get(params)
.success(data=>{
this.classList = data
}).error(response=>{
this.toast(response.body.message||'获取列表失败')
})
},
getMyStudents () {
let uri = util.sprintf('employees/%s/students?class=1',this.eid$)

this.$rest(uri).get()
.success(data=>{
this.studentList.class = data.class
this.studentList.list = data.list
}).error(response=>{
this.toast(response.body.message||'获取列表失败')
})
},
}
}



