
import Vue from 'vue'
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import PlayVideo from 'c%/PlayVideo.vue'
import FileItem from './media/file-item.vue'
import MenuButton from './media/menu-button.vue'
import PushModal from './media/push.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.use(ConfirmPlugin);

export default{
mixins: [ common ],
props: {
mode: {
type: String,
default: 'list'
},
count: {
type: Number,
default: 0
},
fileType: {
type: String,
default: ''
}
},
components: {
Tab,
TabItem,
Swiper,
SwiperItem,
FileItem,
Previewer,
PlayVideo,
TransferDom,
MenuButton,
PushModal
},
data () {
return {
tab: 0,
data_uri: 'files',
fileList: [],
banFileIds: [],
editable: false,
activeVoiceId: 0,
dataList: {
0: [],
1: [],
2: [],
3: []
},
}
},
computed: {
height () {
let height = window.innerHeight
return (height - this.headerHeight - 44) +'px'
},
editable$ () {
return this.editable||this.mode=='select'
},
fileIds () {
let list = this.fileList,
result = []

if(list.length > 0) {
list.forEach(l=>{
result.push(l.file_id)
})
}

return result
}
},
mounted () {
},
methods: {
setActiveVoice (item) {
if(item.media_type=='voice'){
console.log('item.fileId')
console.log(item.file_id)
this.activeVoiceId = item.file_id
}
},
disabledFile (item) {
if(this.count > 0&&this.count == this.fileIds.length){
return this.fileIds.indexOf(item.file_id) > -1
}

return this.banFileIds.indexOf(item.file_id)==-1
},
pushSuccess () {
this.editable = false
this.fileList = []
this.$refs.menu.operate = ''
},
operate (type) {
let map_title = {'del':'删除','push':'推送'}
if(this.fileList.length>0){


if(this.mode=='select'){
this.$emit('on-selected',this.fileList)
return
}

if(type=='del'){
this.delInfo()
}else{
this.$refs.push.show('','add')
}
}
},
delInfo () {
let $this = this
this.$vux.confirm.show({
title: '提示',
content: '确认删除吗',

onCancel () {

},
onConfirm () {
$this.$rest('files/delete_files').post({file_ids:$this.fileIds})
.success(response=>{
$this.dealDelResult(response.data)
}).error(response=>{
$this.toast(response.body.message||'删除失败','warn')
})
}
})
},
dealDelResult (data) {
if(data.fail_num==0){
this.toast('删除成功','success')
this.downCallback()
this.editable = false
this.fileList = []
this.$refs.menu.operate = ''
}else{
let tip = ''
data.error.forEach((e,i)=>{
let index = this.banFileIds.indexOf(e.file_id)
if(index==-1){
this.banFileIds.push(e.file_id)
}
if(i==0){
tip = e.file_name
}
})
this.toast(util.sprintf('%s等%s个文件已被回访或其他服务所使用，禁止删除',tip,data.error.length))
}

},
toggleCheck (item) {
let index = this.fileList.indexOf(item)
if(index>-1){
this.fileList.splice(index,1)
}else{
this.fileList.push(item)
}
},
menuClick (val) {
this.fileList = []
this.editable = val
},
previewImage (item) {
let list = this.dataList[0]
let index = list.indexOf(item)
this.preview({list:list,index:index})
},
previewVideo (item) {
this.$refs.preview_video
.set('video',item)
.show('')
},
hook_get_param (params) {
let map_type = {0:'image',1:'voice',2:'video',3:'file'}
params.media_type = map_type[this.tab]
},
deal_data (response) {
let list = response.body.data.list,
total = response.body.data.total,
curLength = 0

if(this.pageIndex===1){
this.dataList[this.tab] = []
}

this.dataList[this.tab] = this.dataList[this.tab].concat(list)

this.mescroll.endBySize(list.length, total)
},
}

}



