	import common from '@/libs/common.mixin'
	import globals from '@/libs/globals.mixin'
	
	const idField = 'did'
	const textField = 'title'
	
	export default{
		mixins: [ common,globals ],
		props: {
			title: {
				type: String,
				default: ''
			},
			textField: {
				type: String,
				default:()=>{
					return textField
				} 
			},
			idField: {
				type: String,
				default: ()=>{
					return idField
				} 
			},
			dictName: {
				type: String,
				default: ''
			},
			gvarName: {
				type: String,
				default: ''
			},
			value: {
				type: [String,Number,Array],
				default: ()=>{
					return []
				}
			},
			dataList: {
				type: Array,
				default: ()=>{
					return []
				}
			}
		},
		data () {
			return {
				toggleExpand: false,
				model: this.value
			}
		},
		watch: {
			model: function (val) {
				this.$emit('input',val)
			},
			value: function (val) {
				this.model = val
			}
		},
		methods: {
			toggleCheck (row) {
				if(this.multiple){
					let index = this.model.indexOf(row[this.idField])
					if(index>-1){
						this.model.splice(index,1)
					}else{
						this.model.push(row[this.idField])
					}	
				}else{
					this.model = row[this.idField]
				}						
			},
			getItemIndex (row) {
				let index = -1
				
				if(this.multiple) {
					index = this.model.indexOf(row[this.idField])
				}else{
					if(this.model==row[this.idField]){
						index = 1						
					}
				}				
				return index
			}
		},
		computed: {
			list () {
				let list = this.dataList||[]
				if(this.dictName){
					list = this.dicts(this.dictName)
				}
				else if (this.gvarName) {
					list = this.gvars(this.gvarName)
				}
				return list 
			},
			multiple(){
                if(Array.isArray(this.value)){
                    return true
                }
                return false
            },
            selectedText () {
            	let model = this.model,
            		result = ''
            	if(this.multiple) {
            		if(model.length){
            			model.forEach(m=>{
            				result += this.labelDicts(m,this.dictName)+','
            			})
            			result = result.substring(0,result.length-1)
            		}
            	}else{
            		result = this.labelDicts(model,this.dictName)
            	}
            	return result	
            }
		}
	}



